@use 'base/variables' as v;
@use 'base/mixins' as m;

.artistas {
    background-color: v.$negroCarbon;
    padding: 3rem 0;
    h2, p {
        
        text-align: center;
        color: v.$rosa;
    }
    .dia {
        text-transform: uppercase;
        margin-bottom: 5rem;
    }
    .calendario {
        @include m.resetear-lista;
        li{
            padding: 2.5rem;
            font-weight: 700;
            span {
                font-weight: 400;
            }
        }
        color: v.$negroCarbon;
    }
}

.escenarios-contenedor{
    @include m.tablet {
        @include m.grid(3, 5rem)
    }
    p {
        color: v.$blanco;
    }
}
.escenario {
    padding-top: 2rem;
    margin: 1rem 0;
    .nombre-escenario {
        &:after {
            content: '';
            display: block;
            width: 20rem;
            height: 10rem;
            background-size: 15rem;
            background-repeat: no-repeat;
            background-position: center center;
            margin: 2rem auto 0 auto;
        }
    }
}
.edm .nombre-escenario:after {
    background-image: url(../img/icono_edm.png);
}
.rock .nombre-escenario:after {
    background-image: url(../img/icono_rock.png);
}

.bg-amarillo {
    background-color: v.$verde;
    li:nth-child(odd) {
        background-color: darken(v.$verde, 10%);
    }
   
}
.bg-verde {
    background-color: v.$verde;
    li:nth-child(odd) {
        background-color: darken(v.$verde, 10%);
    }
}
